import React, { Component } from 'react';
import './App.css';


class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      question: "",
      appear: true
    }
  }

  QuestionInput = (e) => {
    this.setState({
      question: e.target.value,
    })
  }

  AskIt = () => {
    var magicNumber = Math.floor(Math.random() * 21);
    if (this.state.question === "") {
      this.setState({
        index: 0,
      });
    }
    else {

      this.setState({
        index: magicNumber,
      });

    }

  }
  render() {
    const possibleAnswers = [
      '',
      'It is certain',
      'It is decidedly so',
      'Without a doubt',
      'Yes, definitely',
      'You may rely on it',
      'As I see it, yes',
      'Outlook good',
      'Yes',
      'Signs point to yes',
      'Reply hazy try again',
      'Ask again later',
      'Better not tell you now',
      'Cannot predict now',
      'Concentrate and ask again',
      'Don\'t count on it',
      'My reply is no',
      'My sources say no',
      'Most likely',
      'Outlook not so good',
      'Very doubtful'
    ];

    return (

      <div className="App">
        
        <div className="acme">
        
          <div className = "Heading" >
        
            <h1 className = "htext">Magic 8 Ball</h1>
        </div>
        
          <h1>Ask me any Question,</h1>
          <h5>but only yes no questions,okay</h5>
        </div>

        <div>
          <input className="inputQues" onChange={this.QuestionInput} value={this.state.question} placeholder="What is your Question?"></input>
          <br />
          <button onClick={this.AskIt} className="Askbtn" >Ask</button>
        </div>
        <br />
        
        <svg className="resultDiv">
          <circle cx={500} cy={300} r={300} fill="#000000" />
          <circle cx={500} cy={300} r={150} fill="#0c0c0c" stroke="#fff" />
          <circle cx="630" cy="90" r="20"
            style={{ fill: '#fff', stroke: '#ffe', strokeWidth: '2' }} />
          <path d="M 380 250 L 625 250 L 500 420 Z" fill="blue" />

          <text className="AnsText" x="50%" y="50%" fill="#FFF" fontSize="18" fontFamily="Chakra Petch" textAnchor="middle">{possibleAnswers[this.state.index]}</text>

        </svg>

      </div>
    );
  }
}

export default App;
